import { ChangeDetectorRef, Component, EventEmitter, HostListener, OnInit, Output } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatRadioChange } from '@angular/material/radio';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DomSanitizer } from '@angular/platform-browser';
import { SortCriteria } from 'app/shared/pipes/custom-task-summary-sorting.pipe';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { TasksummaryService } from '../tasksummary.service';
import * as _ from 'lodash';
import { LoanAccountApproverComponent } from '../loan-account-approver/loan-account-approver.component';


@Component({
  selector: 'app-loan-account-task-summary',
  templateUrl: './loan-account-task-summary.component.html',
  styleUrls: ['./loan-account-task-summary.component.scss']
})
export class LoanAccountTaskSummaryComponent implements OnInit {
  accountId: any;
  searchText;
  statusArr: any = ['BACKLOG', 'ONGOING', 'APPROVED', 'REJECTED']
  arr: any = ['BACKLOG', 'ONGOINGArr', 'APPROVEDArr', 'REJECTEDArr']
  BACKLOGArr: any = [];
  ONGOINGArr: any = [];
  REJECTEDArr: any = [];
  APPROVEDArr: any = [];
  
  BACKLOGTotalPages: number = 0
  ONGOINGTotalPages: number = 0
  REJECTEDTotalPages: number = 0
  APPROVEDTotalPages: number = 0

  BACKLOGCount: number = 0
  ONGOINGCount: number = 0
  REJECTEDCount: number = 0
  APPROVEDCount: number = 0

  BACKLOGImage: any[] = [];
  ONGOINGImage: any[] = [];
  REJECTEDImage: any[] = [];
  APPROVEDImage: any[] = [];

  BACKLOGTotalRecords: number = 0;
  ONGOINGTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  BACKLOGPage: number = 0;
  ONGOINGPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;

  loading: boolean;

  image: Object;
  searchable:boolean=false;
  screenType = "loan";
  isScroll:boolean = true;
  //variable for sorting data
  public criteria: SortCriteria;

  @Output() setValue: EventEmitter<string> = new EventEmitter();
  private _searchSubject: Subject<string> = new Subject();
  radioButtonSelected: any;

  public listOfOptions: any = [
    { label: 'Corporate ID', value: 'corporateID', checked: true },
    { label: 'Name ', value: 'firstName', checked: false }
  ];
  noDataFound: boolean = false;

  constructor(public dialog: MatDialog, private ls: LocalStoreService, private taskSumService: TasksummaryService,
    public cdr: ChangeDetectorRef, private apiService: ApiService, private snack: MatSnackBar, private sanitizer: DomSanitizer) { 
      this._setSearchSubscription();
    }
    @HostListener('scroll', ['$event'])
    onScroll(event: any) {  
      if(this.isScroll){
        this.BACKLOGPage++;
        this.getDataOnScroll("BACKLOG",this.BACKLOGPage,this.BACKLOGTotalPages);    
        this.ONGOINGPage++;
        this.getDataOnScroll("ONGOING",this.ONGOINGPage, this.ONGOINGTotalPages);
        this.APPROVEDPage++;
        this.getDataOnScroll("APPROVED", this.APPROVEDPage, this.APPROVEDTotalPages);
        this.REJECTEDPage++;
        this.getDataOnScroll("REJECTED", this.REJECTEDPage, this.REJECTEDTotalPages);
      }
    }
  
    ngOnInit(): void {
      var task:boolean=true;
      this.apiService.setTask(task);
    this.accountId = this.ls.getItem('accountId');

    /* @called :: on screen load */
    this.getDataOnLoad();



    this.listOfOptions.filter(item => {
      if (item.checked) {
        this.radioButtonSelected = item.value;
      }
    });

    this.criteria = {
      property: this.radioButtonSelected,
      descending: true
    };
  }
  radioChange(event: MatRadioChange, data) {
    this.radioButtonSelected = data.value;
    this.criteria.property=this.radioButtonSelected;
  }

  public updateSearch(searchTextValue: string) {
    console.log("searchTextValue::",searchTextValue)
    this._searchSubject.next(searchTextValue);
  }
  private _setSearchSubscription() {
    this._searchSubject.pipe(
      debounceTime(1000)
    ).subscribe((searchValue: string) => {
      this.setValue.emit(searchValue);
      console.log('search value :: ', searchValue)
      console.log('search text :: ', this.searchText);
      if (searchValue == null || searchValue == '' || searchValue == "") {
        this.noDataFound = false;
        this.searchable=false;
        this.getDataOnLoad();
        return;
      }
      this.searchcorporateID(searchValue);
    });
  }

  clear(){
    this.searchText = "";
    this.isScroll = true;
    this.getDataOnLoad();
  }

  /* @method :: to load all data on screen load */
  async getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchCustomerInfo(statusItem);
    }
  }
  fetchCustomerInfo(status:string){
   
    this.taskSumService.getallLoanAccountData(status)
            .subscribe((custResp:any) => {
              console.log("Response", custResp);
              this.updateStatusResponse(status, custResp);
            });
  }

  updateStatusResponse(status, data){
    console.log("data", data)
    console.log("status", status)
    if(status == 'BACKLOG'){
      this.BACKLOGArr = data?.loanList;
      this.BACKLOGTotalPages = data?.totalPages;
      this.BACKLOGTotalRecords = data?.totalNoOfElements;
    }else if(status == 'ONGOING'){
      this.ONGOINGArr = data?.loanList;
      this.ONGOINGTotalPages = data?.totalPages;
      this.ONGOINGTotalRecords = data?.totalNoOfElements;
    }else if(status == 'APPROVED'){
      this.APPROVEDArr = data?.loanList;
      this.APPROVEDTotalPages = data?.totalPages;
      this.APPROVEDTotalRecords = data?.totalNoOfElements;
    }else if(status == 'REJECTED'){ 
      this.REJECTEDArr = data?.loanList;
      this.REJECTEDTotalPages = data?.totalPages;
      this.REJECTEDTotalRecords = data?.totalNoOfElements;
      console.log("this.REJECTEDArr",this.REJECTEDArr);
    }

    this.cdr.markForCheck();
}
  

  /* @method :: to load data on scroll of data*/
  onScrollingFinished(e, status) {
    if(status == 'BACKLOG'){
      this.BACKLOGPage++;
      this.getDataOnScroll(status,this.BACKLOGPage,this.BACKLOGTotalPages);
      
     }else if(status == 'ONGOING'){
     this.ONGOINGPage++;
     this.getDataOnScroll(status,this.ONGOINGPage, this.ONGOINGTotalPages);
       
     }else if(status == 'APPROVED'){
       this.APPROVEDPage++;
       this.getDataOnScroll(status, this.APPROVEDPage, this.APPROVEDTotalPages);
     }else if(status == 'REJECTED'){ 
       this.REJECTEDPage++;
       this.getDataOnScroll(status, this.REJECTEDPage, this.REJECTEDTotalPages);
     }
  }

  /* @method :: to call service ,when scroll method called */
  getDataOnScroll(status,page,totalPage) {
    console.log('---------scrolled----------')
    if (page === totalPage) {
      console.log('No scroll requied')
      return;
    }

    this.taskSumService.getAllLoanDetailOnScroll(status, page)
      .subscribe(data => {
        console.log('count data :: ', data);
        this.cdr.markForCheck();
        this.updateDataOnScroll(status, data.loanList);
      });

  }

  goToLoanAccountSummary(data, status, colCode) {

      localStorage.setItem('CustomerColorCode', colCode);
    const dialogRef = this.dialog.open(LoanAccountApproverComponent,
      {
        panelClass: 'full-width-dialog',
        data: { dataResp: data, recordStatus: status },
        width: '80%',
        disableClose: false
      });
    dialogRef.afterClosed().subscribe((submit) => {
      console.log('dialog closed:: ', submit)
      if (submit.recordStatus === 'REJECTED' || submit.recordStatus === 'APPROVED'
        || submit.data.kycStatus === 'REJECTED' || submit.data.kycStatus === 'APPROVED') {
        this.getDataOnLoad()
      }
      this.getDataOnLoad()

    })
  }

  searchcorporateID(searchTerm) {
    let text:any;
      let params:any;
      if(searchTerm===''){
        console.log('empty')
        this.getDataOnLoad();
        this.isScroll = true
        return;
      }
      if(this.isNum(searchTerm)){
        if(searchTerm.length == 10){
          console.log("contact");
          text=this.searchText.trim();
          params = 'phoneNumber='+text;
        }
        else{
          console.log("Id");
          text=this.searchText.trim();
          // params = 'loanAccountId='+text;
          // params = 'customerId='+text;
          this.taskSumService.getallLoanAccountData2(text)
          .subscribe(data => {
            console.log(data)
            if(data != null){
         
              params = 'loanAccountId='+text;
              console.log('loan',params);
            }
            else{
              console.log('not a loan')
              params = 'customerId='+text;
              console.log('customer',params);
            }
          })
        }
      }
      else if(this.isString(searchTerm)){
        if( /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(searchTerm)){
          console.log("email");
          text=this.searchText.trim();
          params = 'primaryEmailAdress='+text;
        }
        else{
        console.log("name");
        text=this.searchText.trim();
        params = 'customerName='+text;
        }
      }
      setTimeout(() => {
        this.getCustomerData(params);
      }, 100);
      
      this.isScroll = false
  }

  getCustomerData(params) {
    console.log(params);
    this.loading = true;
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
     // this.fetchCustomerByStatus(statusItem,params,0);
     if(statusItem == 'BACKLOG'){
    this.taskSumService.getallLoanAccountData1(params,statusItem)
    .subscribe(data => {
      this.BACKLOGTotalPages = data?.totalPages;
      this.loading = false;
      console.log('resp data :: ', data);
      this.BACKLOGArr = [];
      for (let index = 0; index < data?.loanList.length; index++) {
        const element = data?.loanList[index];
        this.BACKLOGArr.push(element);
        
      }
      
    })
  }
  else  if(statusItem == 'ONGOING'){
    this.taskSumService.getallLoanAccountData1(params,statusItem)
    .subscribe(data => {
      this.ONGOINGTotalPages = data?.totalPages;
      this.loading = false;
      console.log('resp data :: ', data);
      this.ONGOINGArr = [];
      for (let index = 0; index < data?.loanList.length; index++) {
        const element = data.loanList[index];
        this.ONGOINGArr.push(element);
        
      }
       
    })
  }
  else  if(statusItem == 'APPROVED'){
    this.taskSumService.getallLoanAccountData1(params,statusItem)
    .subscribe(data => {
      this.APPROVEDTotalPages = data?.totalPages;
      this.loading = false;
      console.log('resp data :: ', data);
      this.APPROVEDArr = [];
      for (let index = 0; index < data?.loanList.length; index++) {
        const element = data.loanList[index];
        this.APPROVEDArr.push(element);
        
      }
       
    })
  }
  else  if(statusItem == 'REJECTED'){
    this.taskSumService.getallLoanAccountData1(params,statusItem)
    .subscribe(data => {
      this.REJECTEDTotalPages = data?.totalPages;
      this.loading = false;
      console.log('resp data :: ', data);
      this.REJECTEDArr = [];
      for (let index = 0; index < data?.loanList.length; index++) {
        const element = data?.loanList[index];
        this.REJECTEDArr.push(element);
        
      }
    })
  }
  }
  }

  // getCustomerData(params) {
  //   this.loading = true;
  //   console.log("params::",params)
  //   this.apiService.getallLoanAccountData(params)
  //   .subscribe(data => {
  //     this.loading = false;
  //     console.log('resp data :: ', data);
  //    this.resetSearchData();
  //     for (let index = 0; index < data.loanList.length; index++) {
  //       const element = data.loanList[index];
  //       this.searchResponseUpdate(element.status,data.loanList[index]);
        
  //     }
  //   })
  // }

  resetSearchData(){
    this.BACKLOGArr = [];
    this.ONGOINGArr = [];
    this.APPROVEDArr = [];
    this.REJECTEDArr = [];
    this.cdr.markForCheck();
  }

  searchResponseUpdate(status:string, data: any) {
    if(status == 'ONGOING'){
      // this.ONGOINGArr = [];
      this.ONGOINGArr.push(data);
     }else if(status == 'BACKLOG'){
      // this.BACKLOGArr = [];
      this.BACKLOGArr.push(data);
     }else if(status == 'APPROVED'){
      // this.APPROVEDArr = [];
      this.APPROVEDArr.push(data);
     }else if(status == 'REJECTED'){ 
      // this.REJECTEDArr = [];
      this.REJECTEDArr.push(data);
     }
    this.cdr.markForCheck();
  }

  getProfileImage(corporateID){
    this.apiService.getProfileDetails(corporateID)
    .subscribe((data:any) => {
      console.log(data);
      console.log(data.profileImage); 
      this.searchable=false;
      if(data.profileImage!=null){
      let objectURL = 'data:image/jpeg;base64,' + data.profileImage.fileData;
      this.searchable=true;
      this.image = this.sanitizer.bypassSecurityTrustUrl(objectURL);
      console.log(this.searchable,"Searchable");
      }
     
    },(error:any) => {
      console.log(error);
    });
  }
  getcorporateIDData(valueToFilter) {
    console.log('get kyc')
    if (valueToFilter != '' && valueToFilter != undefined && valueToFilter != null) {
      this.apiService.getKycTaskSummaryAll(valueToFilter, this.searchText)
        .subscribe(data => {
          console.log('resp data :: ', data);
          this.cdr.markForCheck();
            this.cdr.detectChanges();
          if (data != null) {
            var value = data[0].customerAdditionalInfo.kycStatus + 'Arr';
            this[value].splice(0, 0, data[0])
            this[value] = this[value].slice();
            console.log('resp value :: ', this[value]);
          } else {
            this.noDataFound = true;
          }

        })
    }
  }

  ngOnDestroy() {
    this._searchSubject.unsubscribe();

    this.ONGOINGArr = [];
    this.APPROVEDArr = [];
    this.REJECTEDArr = [];
    this.BACKLOGArr = [];

    this.BACKLOGPage = 0;
    this.REJECTEDPage = 0;
    this.APPROVEDPage = 0;
    this.ONGOINGPage = 0;

    this.BACKLOGTotalPages = 0;
    this.REJECTEDTotalPages = 0;
    this.APPROVEDTotalPages = 0;
    this.ONGOINGTotalPages = 0;
  }

  /* @method :: to update the existing array with scroll api response */
  async updateDataOnScroll(status:string, data: any) {
    if(status == 'BACKLOG'){
      const result = _.unionBy(this.BACKLOGArr, data, 'loanAccountId');
      this.BACKLOGArr = result;
     }else if(status == 'ONGOING'){
      const result = _.unionBy(this.ONGOINGArr, data, 'loanAccountId');
      this.ONGOINGArr = result;
     }else if(status == 'APPROVED'){
      const result = _.unionBy(this.APPROVEDArr, data, 'loanAccountId');
      this.APPROVEDArr = result;
     }else if(status == 'REJECTED'){ 
      const result = _.unionBy(this.REJECTEDArr, data, 'loanAccountId');
      this.REJECTEDArr = result;
     }
    this.cdr.markForCheck();
  }

  
  isNum(val){
    return !isNaN(val)
  }

  isString(val){
    return val;
  }

  getRandomColor(idx) {
    var col0 = '#0d856b';
    var col1 = '#d66f1b';
    var col2 = '#9f52e7';
    var col3 = '#e9318d';
    var col4 = '#1175a3';
    var col5 = '#e93131';
    var col6 = '#2316d3';
    var col7 = '#f557f5';
    var col8 = '#d6c31b';
    var col9 = '#40d61b';

    if ((idx % 10) == 0) return col0;
    if ((idx % 10) == 1) return col1;
    if ((idx % 10) == 2) return col2;
    if ((idx % 10) == 3) return col3;
    if ((idx % 10) == 4) return col4;
    if ((idx % 10) == 5) return col5;
    if ((idx % 10) == 6) return col6;
    if ((idx % 10) == 7) return col7;
    if ((idx % 10) == 8) return col8;
    if ((idx % 10) == 9) return col9;
    return '#d86315';

  }


}
